.home-page {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 100%;
      height: calc(100vh - 80px);
}
.maps-container {
      width: 100%;
      height: 100%;
      background: #0D2847;
      padding: 100px 0;
}

.maps {
      height: 80vh;
      width: 80vw;
      margin: auto;

      @media (max-width: 620px) {
            width: 95%;
      }
}