.boxes {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

}
.box {
      width: 30%;
      padding: 50px 0 150px 0;
      margin-left: 20px;

      @media (max-width: 1250px) {
            width: 40%;
      }
      @media (max-width: 800px) {
            width: 80%;
            margin-left: 0;
      }

      &:first-child {
            margin-left: 0;
      }

      &_main {
            display: flex;
            position: relative;
            z-index: 1;
      }

      &_day-info {
            width: 50%;
            background: #D1F6FD;
            border-radius: 30px 0 0 0;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
      }

      &_date {
            font-weight: 700;
            font-size: 22px;
            padding-top: 20px;
      }

      &_icon {
            width: 100px;
            padding: 20px 0;

            & img {
                  width: 100%;
            }
      }

      &_temp {
            font-weight: 700;
            font-size: 35px;
      }

      &_text {
            font-weight: 700;
            font-size: 18px;
            padding-bottom: 20px;
            text-align: center;
      }

      &_night-info {
            width: 50%;
            background:#000 url('http://www.script-tutorials.com/demos/360/images/stars.png') repeat top center;
            border-radius: 0 30px 0 0;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            color: #D1F6FD;
      }
}

.box_sub {
      display: flex;
      width: 100%;
      position: absolute;
      bottom: -20px;
      left: 0;
      z-index: -1;
      transition: 1s;

      & span {
            padding: 5px;
            font-size: 14px;
            font-weight: 500;
      }

      & span:last-child {
            padding-bottom: 20px;
      }
}
.radius-day {
      border-radius: 0 0 0 20px;
}
.radius-night {
      border-radius: 0 0 20px 0;
}